<template>
	<section>
		<header class="header">
			<div class="topbar clearfix">
				<div class="topbar-left">
					<div class="top-logo">
						<router-link to="/" class="logo">logo</router-link>
					</div>
				</div>
				<div class="topbar-right">
					<div class="search" @click="searchEvent">
						<el-input placeholder="" icon="search" v-model.trim="searchContent" :on-icon-click="handleSearch" @focus="handleFocus" @blur="handleBlur" @input="handleInput" v-bind:style="{ width: inputWidth }" class="transition"/>
						<div class="search-suggest" v-show="suggestShow">
							<ul v-loading="loading">
								<li v-for="(item, index) in searchList" ref="suggest" @click="suggestSearch(index)">
									<i class="el-icon-search"></i>
									<span>{{searchContent}}{{item}}</span>
								</li>
							</ul>
						</div>
					</div>
					<div class="link">
						<p>010-56143517</p>
					</div>
					<div class="welcome">
						<div v-if="user">
							<el-popover
							  ref="popover2"
							  placement="bottom"
							  title=""
							  width="220"
							  trigger="hover"
							  :content="''+user.loginTime">
							</el-popover>
							<span v-popover:popover2 @click="userShow">Hello, {{user.name}}</span>
							<span @click="handleExit"></span>
						</div>
						<div v-else class="login">
							<a href="javascript:;" @click="handleLogin"></a>
						</div>
					</div>
				</div>
			</div>
			<div class="navbar clearfix">
				<div class="nav-menu">
					<h3> 
						<i class="el-icon-arrow-up"></i>
						<i class="el-icon-arrow-down"></i>
					</h3>
					<ul class="menu-list">
						<li v-for="(item, index) in menuList" :key="index" class="menu-item">
							<router-link to="/list" class="title">
								{{item.name}}
								<i class="el-icon-arrow-right"></i>
							</router-link>
							<div class="submenu-list">
								<router-link to="/detail?wareName=100000000201708" v-for="(item, index) in item.children" :key="index" class="submenu-item">
									{{item.name}}
								</router-link>
							</div>
						</li>
					</ul>
				</div>
				<ul class="nav-list">
					<li v-for="(item, index) in navList" :key="index" class="nav-item">
						<router-link :to="item.to">{{item.name}}</router-link>
					</li>
				</ul>
			</div>
		</header>
		<el-dialog 
			size="tiny" 
			title="" 
			:visible.sync="loginVisible">
			<el-form :model="loginForm" ref="loginForm" :rules="rules" label-width="100px">
        <el-form-item label="" prop="username">
          <el-input v-model.trim="loginForm.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=" " prop="password">
          <el-input type="password" v-model.trim="loginForm.password" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
      	<el-button @click="loginVisible = false"></el-button>
      	<el-button type="primary" @click="submitForm"></el-button>
      </div>
		</el-dialog>
	</section>
</template>
