				],
				searchList: ['', '','','','','',''],
			}
		},
		methods: {
			getUserInfo() {
				let user = this.user = JSON.parse(localStorage.getItem('user'));
				let username = localStorage.getItem('username');
				if(username) {
					this.loginForm = {
						username: username,
						password: ''
					}
				}
				if(!user) return;
				let duration = (new Date().getTime() - new Date(this.user.loginTime).getTime()) / 1000 / 60;
				if (Math.ceil(duration) > 30) {
					this.$message('')
					localStorage.removeItem('user')
					this.user = null;
					setTimeout(() => {
						this.loginVisible = true;
					}, 2000)
				}
			},
			searchEvent(e) {
				e.stopPropagation()
			},
			handleSearch() {
				if(!this.searchContent) return;
				this.loading = true;
				setTimeout(() => {
					this.$notify.warning({
	          title: '',
	          message: ` ${this.searchContent}`,
	        })
	        this.loading = false;
	        this.suggestShow = false;
				}, 2000)
			},
			handleFocus() {
				this.inputWidth = '300px';
				this.suggestShow = this.searchContent ? true : false;
				return false;
			},
			handleBlur() {
				if(this.searchContent) return;
				this.inputWidth = '200px'
			},
			handleInput() {
				this.suggestShow = this.searchContent ? true : false
			},
			suggestSearch(index) {
				let content = this.$refs.suggest[index].innerText.substr(3,)
				this.loading = true;
				setTimeout(() => {
					this.$notify.warning({
	          title: '',
	          message: `${content}`,
	        })
	        this.loading = false
	        this.suggestShow = false
				}, 2000)
			},
			submitForm() {
				this.$refs.loginForm.validate(valid => {
					if(valid) {
						let loginUser = this.userList.filter(user => {
							if(user.username === this.loginForm.username) return true;
							return false;
						})
						if(!loginUser[0]) {
							return this.$message.error('')
						}
						if(loginUser[0].password === this.loginForm.password) {
							this.$message.success('')
							this.user = loginUser[0];
							this.user.loginTime = this.$moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
							localStorage.setItem('username', this.user.username)
							localStorage.setItem('user', JSON.stringify(this.user))
							this.loginForm = {
								username: '',
								password: ''
							}
							this.loginVisible = false
						} else {
							return this.$message.error('')
						}
					} else {
						console.log('err submit')
					}
				})
			},
			userShow() {
				this.$router.push('orderList')
			},
			handleLogin() {
				this.loginVisible = true;
			},
			handleExit() {
				this.$confirm('', '', {type: 'warning'}).then(() => {
						localStorage.removeItem('user')
						this.$message.success('')
						this.user = null;
				}).catch(err => {
					this.$message('')
				})
			}
		},
		created () {
			this.getUserInfo()
		},
		mounted() {
			document.body.addEventListener('click', (e) => {
