			.logo {
				display: inline-block;
				width: 118px;
				height: 43px;
				margin-top: 4px;
				text-indent: -2000em;
				background: url(../assets/img/logo.png) no-repeat;
				background-size: cover
			}
		}
	}
	.topbar-right {
		float: right;
		line-height: 50px;
		color: #fff;
		div {
			display: inline-block;
			font-size: 14px;
			&:last-child {
				margin-right: 0
			}
		}
	}
	.search {
		position: relative;
		margin-right: 15px;
		.search-suggest {
			position: absolute;
			top: 42px;
			left: 0;
			right: 15px;
			z-index: 999;
			color: #666;
			background: #fff;
			box-shadow: 0 3px 5px 3px rgba(0,0,0,.3);
			li {
				padding: 5px 15px;
				line-height: 1.5;
				cursor: pointer;
				word-wrap: break-word;
				&:hover {
					color: #fff;
					background: #0094DA
				}
				i {
					margin-right: 5px
				}
			}
		}
	}
	.welcome {
		> div {
			margin: 0 15px;
		}
		span {
			margin-left: 10px;
			cursor: pointer;
		}
		.login {
			a {
				color: #fff;
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
	.navbar {
		width: 1180px;
		height: 60px;
		margin: 0 auto;
		line-height: 60px;
		background: #333
	}
	.nav-menu, .nav-list {
		float: left;
		color: #333;
		border: 0
	}
	.nav-menu {
		height: 60px;
		&:hover {
			.menu-list{
				display: block;
			}
			h3 {
				.el-icon-arrow-up {
					display: block;
				}
				.el-icon-arrow-down {
					display: none;
				}
			}
		}
		h3 {
			width: 200px;
			text-align: center;
			line-height: 60px;
			cursor: pointer;
			color: #fff;
			position: relative;
			background: rgba(255,255,255,.2);
			i {
				// display: none;
				position: absolute;
				top: 24px;
				right: 15px;
				font-size: 14px;
			}
			.el-icon-arrow-up {
				display: none;
			}
		}
		.menu-list {
			display: none;
			position: absolute;
			z-index: 99;
			width: 200px;
			padding: 3px 0;
			color: #fff;
			font-size: 16px;
			background: rgba(0,0,0,.5);
			.menu-item {
				height: 42px;
				line-height: 42px;
				.title {
					display: inline-block;
					position: relative;
					width: 100%;
					padding-left: 20px;
					color: #fff;
					letter-spacing: 2px;
					i {
						position: absolute;
						top: 15px;
						right: 10px;
						font-size: 12px
					}
				}
				&:hover {
					background: #0094DA;
					.submenu-list {
						display: block;
					}
				}
			}
		}
	}
	.submenu-list {
		display: none;
		position: absolute;
		top: 0;
		left: 200px;
		z-index: 100;
		width: 550px;
		height: 300px;
		line-height: 1.1;
		padding: 20px;
		border-top: 1px solid #e4e4e4;
		background: #fff;
		box-shadow: 1px 1px 3px rgba(0,0,0,.1);
		.submenu-item {
			display: inline-block;
			height: 30px;
			line-height: 30px;
			margin: 0 8px;
			word-wrap: break-word;  
			word-break: normal; 
			color: #333;
			&:hover {
				color: #0094DA;
			}
		}
	}
	.nav-list {
		margin-left: 40px;
		line-height: 60px;
		.nav-item {
			display: inline-block;
			a {
				display: inline-block;
				width: 100%;
				padding: 0 30px;
				color: #fff;
				font-size: 16px;
				&:hover {
					background: rgba(255,255,255,.3);
				}
				&.router-link-active {
					height: 60px;
					color: #0094DA;
					// background: rgba(0,0,0,.05);
					// border-bottom: 3px solid #0094DA;
				}
			}
		}
	}
	.el-form  {
		width: 80%;
		margin: auto;
	}
</style>


